import { Suspense, lazy } from 'react';
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Outlet,
} from 'react-router-dom';
import Preloader from './components/preloader';
import Layout from './layouts';
import Layout02 from './layouts/layout-02';
// AWS Amplify
import { Amplify } from 'aws-amplify';
import awsExports from './aws-exports';
Amplify.configure(awsExports);
//Redux
// import { useAppSelector } from './redux/hooks';
// import { IUser } from '@sieloapp/shared/types';
// import { IUser, IAWSresponse } from '@sieloapp/shared/types';
import SignOut from './components/signout';
import { useSelector } from 'react-redux';
import { RootState } from './redux/store';

// Classic Pages

const DashboardOne = lazy(() => import('./pages/dashboard-one'));
const SignIn = lazy(() => import('./pages/signin'));
const SignUp = lazy(() => import('./pages/signup'));
const VerifyAccount = lazy(() => import('./pages/verify-account'));
const ForgotPassword = lazy(() => import('./pages/forgot-password'));
const ErrorNotFound = lazy(() => import('./pages/error-404'));
const Error500 = lazy(() => import('./pages/error-500'));
const Error503 = lazy(() => import('./pages/error-503'));
const Error505 = lazy(() => import('./pages/error-505'));
const ProfileView = lazy(() => import('./pages/profile-view'));
const Connections = lazy(() => import('./pages/connections'));
const Groups = lazy(() => import('./pages/groups'));
const Events = lazy(() => import('./pages/events'));
// const Search = lazy(() => import('./pages/search'));
const Search = lazy(() => import('./pages/searchWrapper'));

// Classic Plus Pages

const ClassicPlusSignIn = lazy(() => import('./pages/classic-plus/signin'));
const ClassicPlusSignUp = lazy(() => import('./pages/classic-plus/signup'));
const ClassicPlusVerifyAccount = lazy(
  () => import('./pages/classic-plus/verify-account'),
);
const ClassicPlusForgotPassword = lazy(
  () => import('./pages/classic-plus/forgot-password'),
);
const ClassicPlusErrorNotFound = lazy(
  () => import('./pages/classic-plus/error-404'),
);
const ClassicPlusError500 = lazy(
  () => import('./pages/classic-plus/error-500'),
);
const ClassicPlusError503 = lazy(
  () => import('./pages/classic-plus/error-503'),
);
const ClassicPlusError505 = lazy(
  () => import('./pages/classic-plus/error-505'),
);

const ClassicPlusProfileView = lazy(
  () => import('./pages/classic-plus/profile-view'),
);
const ClassicPlusConnections = lazy(
  () => import('./pages/classic-plus/connections'),
);
const ClassicPlusGroups = lazy(() => import('./pages/classic-plus/groups'));

type TuserVerified = 'verified' | 'unverified' | 'unknown' | undefined;

const App = () => {
  // const user: IUser = useAppSelector((state) => state?.user);
  const user = useSelector((state: RootState) => state.user);
  let userVerified: TuserVerified;
  userVerified = undefined;
  if (user.verified === undefined) {
    userVerified = 'unknown';
  } else if (user.verified === true) {
    userVerified = 'verified';
  } else if (user.verified === false) {
    userVerified = 'unverified';
  } else {
    userVerified = 'unknown';
  }

  return (
    <>
      <Router>
        <Suspense fallback={<Preloader />}>
          <Routes>
            {/* Classic Routes */}
            <Route
              element={
                <Layout
                  hideHeader={userVerified !== 'verified' ? true : false}
                  hideFooter={userVerified !== 'verified' ? true : false}
                  hasSidebar={userVerified === 'verified' ? true : false}
                >
                  <Outlet />
                </Layout>
              }
            >
              {/* Authentication Routes */}
              <Route
                path="/signin"
                element={userVerified !== 'verified' ? <SignIn /> : <Search />}
              />
              <Route
                path="/signup"
                element={userVerified !== 'verified' ? <SignUp /> : <Search />}
              />
              {userVerified === 'unverified' && (
                <Route path="/verify-account" element={<VerifyAccount />} />
              )}
              <Route
                path="/forgot-password"
                element={
                  userVerified !== 'verified' ? <ForgotPassword /> : <Search />
                }
              />
              <Route
                path="/signout"
                element={userVerified !== 'verified' ? <SignIn /> : <SignOut />}
              />
              {/* Dashboard Routes */}
              <Route
                path="/"
                element={userVerified !== 'verified' ? <SignIn /> : <Search />}
              />
              <Route
                path="/dashboard-one"
                element={
                  userVerified !== 'verified' ? <SignIn /> : <DashboardOne />
                }
              />

              {/* Error Routes */}
              <Route
                path="/error-500"
                element={
                  userVerified !== 'verified' ? <SignIn /> : <Error500 />
                }
              />
              <Route
                path="/error-503"
                element={
                  userVerified !== 'verified' ? <SignIn /> : <Error503 />
                }
              />
              <Route
                path="/error-505"
                element={
                  userVerified !== 'verified' ? <SignIn /> : <Error505 />
                }
              />
              <Route
                path="/error-404"
                element={
                  userVerified !== 'verified' ? <SignIn /> : <ErrorNotFound />
                }
              />

              {/* User Routes */}
              <Route
                path="/profile-view"
                element={
                  userVerified !== 'verified' ? <SignIn /> : <ProfileView />
                }
              />
              <Route
                path="/connections"
                element={
                  userVerified !== 'verified' ? <SignIn /> : <Connections />
                }
              />
              <Route
                path="/groups"
                element={userVerified !== 'verified' ? <SignIn /> : <Groups />}
              />
              <Route
                path="/events"
                element={userVerified !== 'verified' ? <SignIn /> : <Events />}
              />

              {/* Other Routes */}
            </Route>

            {/* Apps Routes */}

            {/* App Route With Sidebar Layout two */}
            <Route
              element={
                <Layout
                  hideHeader={userVerified !== 'verified' ? true : false}
                  hideFooter={userVerified !== 'verified' ? true : false}
                  hasSidebar={user.token !== undefined ? true : false}
                  sidebarLayout={2}
                >
                  <Outlet />
                </Layout>
              }
            ></Route>

            {/* App Route With Sidebar*/}
            <Route
              element={
                <Layout
                  hideHeader={userVerified !== 'verified' ? true : false}
                  hideFooter={userVerified !== 'verified' ? true : false}
                  hasSidebar={user.token !== undefined ? true : false}
                >
                  <Outlet />
                </Layout>
              }
            ></Route>

            {/* Classic Plus Routes */}

            <Route
              element={
                <Layout02>
                  <Outlet />
                </Layout02>
              }
            >
              {/* Authentication Routes */}
              <Route
                path="/classic-plus/signin"
                element={
                  userVerified !== 'verified' ? (
                    <SignIn />
                  ) : (
                    <ClassicPlusSignIn />
                  )
                }
              />
              <Route
                path="/classic-plus/signup"
                element={
                  userVerified !== 'verified' ? (
                    <SignIn />
                  ) : (
                    <ClassicPlusSignUp />
                  )
                }
              />
              <Route
                path="/classic-plus/verify-account"
                element={
                  userVerified !== 'verified' ? (
                    <SignIn />
                  ) : (
                    <ClassicPlusVerifyAccount />
                  )
                }
              />
              <Route
                path="/classic-plus/forgot-password"
                element={
                  userVerified !== 'verified' ? (
                    <SignIn />
                  ) : (
                    <ClassicPlusForgotPassword />
                  )
                }
              />

              {/* Error Routes */}
              <Route
                path="/classic-plus/error-500"
                element={
                  userVerified !== 'verified' ? (
                    <SignIn />
                  ) : (
                    <ClassicPlusError500 />
                  )
                }
              />
              <Route
                path="/classic-plus/error-503"
                element={
                  userVerified !== 'verified' ? (
                    <SignIn />
                  ) : (
                    <ClassicPlusError503 />
                  )
                }
              />
              <Route
                path="/classic-plus/error-505"
                element={
                  userVerified !== 'verified' ? (
                    <SignIn />
                  ) : (
                    <ClassicPlusError505 />
                  )
                }
              />
              <Route
                path="/classic-plus/error-404"
                element={
                  userVerified !== 'verified' ? (
                    <SignIn />
                  ) : (
                    <ClassicPlusErrorNotFound />
                  )
                }
              />

              {/* User Routes */}
              <Route
                path="/classic-plus/profile-view"
                element={
                  userVerified !== 'verified' ? (
                    <SignIn />
                  ) : (
                    <ClassicPlusProfileView />
                  )
                }
              />
              <Route
                path="/classic-plus/connections"
                element={
                  userVerified !== 'verified' ? (
                    <SignIn />
                  ) : (
                    <ClassicPlusConnections />
                  )
                }
              />
              <Route
                path="/classic-plus/groups"
                element={
                  userVerified !== 'verified' ? (
                    <SignIn />
                  ) : (
                    <ClassicPlusGroups />
                  )
                }
              />
            </Route>

            {/* Apps Routes */}

            {/* App Route With Minimize Sidebar*/}
            <Route
              element={
                <Layout02 aside="minimize">
                  <Outlet />
                </Layout02>
              }
            ></Route>

            {/* App Route With Sidebar Layout 2*/}
            <Route
              element={
                <Layout02 aside="minimize">
                  <Outlet />
                </Layout02>
              }
            ></Route>

            {/* 404 Page Route */}
            <Route
              element={
                <Layout
                  hideHeader={userVerified !== 'verified' ? true : false}
                  hideFooter={userVerified !== 'verified' ? true : false}
                  hasSidebar={user.token !== undefined ? true : false}
                >
                  <Outlet />
                </Layout>
              }
            >
              <Route path="*" element={<ErrorNotFound />} />
            </Route>
          </Routes>
        </Suspense>
      </Router>
    </>
  );
};

export default App;
